<template>
  <v-container class="rounded-container">
    <v-row class="mb-4">
      <v-col>
        <h1>Заполните результаты игры</h1>
      </v-col>
    </v-row>

    <!-- Team Scores Input -->
    <v-row class="mb-4">
      <v-col>
        <v-text-field v-model="team1Score" label="Счет команды 1" type="number"></v-text-field>
      </v-col>
      <v-col>
        <v-text-field v-model="team2Score" label="Счет команды 2" type="number"></v-text-field>
      </v-col>
    </v-row>

    <!-- Team Tables -->
    <v-row class="mb-4">
      <v-col>
        <v-data-table-virtual :headers="headers" :items="team1" :items-per-page="5" class="table-style">
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title>Команда 1</v-toolbar-title>
            </v-toolbar>
          </template>
          <template v-slot:item="{ item }">
            <tr>
              <td><strong>ID</strong>: {{ item.id }}</td>
              <td><strong>Name</strong>: {{ item.name }}</td>
              <td>
                <v-text-field v-model="item.kill" label="Kill" type="number" color="primary" variant="underlined"/>
              </td>
              <td>
                <v-text-field v-model="item.assiste" label="Assiste" type="number" color="primary" variant="underlined"/>
              </td>
              <td>
                <v-text-field v-model="item.death" label="Death" type="number" color="primary" variant="underlined"/>
              </td>
            </tr>
          </template>
        </v-data-table-virtual>
      </v-col>

      <v-col>
        <v-data-table-virtual :headers="headers" :items="team2" :items-per-page="5" class="table-style">
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title>Команда 2</v-toolbar-title>
            </v-toolbar>
          </template>
          <template v-slot:item="{ item }">
            <tr>
              <td><strong>ID</strong>: {{ item.id }}</td>
              <td><strong>Name</strong>: {{ item.name }}</td>
              <td>
                <v-text-field v-model="item.kill" label="Kill" type="number" color="primary" variant="underlined"/>
              </td>
              <td>
                <v-text-field v-model="item.assiste" label="Assiste" type="number" color="primary" variant="underlined"/>
              </td>
              <td>
                <v-text-field v-model="item.death" label="Death" type="number" color="primary" variant="underlined"/>
              </td>
            </tr>
          </template>
        </v-data-table-virtual>
      </v-col>
    </v-row>

    <!-- Calculate Results Button -->
    <v-row class="mb-4">
      <v-col>
        <v-btn @click="calculateResults"
               border
               class="text-none"
               prepend-icon="mdi-check"
               variant="text"
               color="indigo"
        >
          Сгенерировать результат
        </v-btn>
      </v-col>
    </v-row>

    <!-- Result Display -->
    <v-row>
      <v-text-field v-model="resultString" readonly label="Results"></v-text-field>
    </v-row>
    <v-row>
      <v-col>
        <v-btn class="me-2 text-none"
               color="#4f545c"
               variant="flat"
               @click="copyToClipboard">
          Скопировать результат игры
        </v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      team1: [],
      team2: [],
      team1Score: 0,
      team2Score: 0,
      resultString: '',
      headers: [
        {text: 'ID', value: 'id'},
        {text: 'Name', value: 'name'},
        {text: 'Kill', value: 'kill'},
        {text: 'Assiste', value: 'assiste'},
        {text: 'Death', value: 'death'},
      ],
    };
  },
  created() {
    this.parseUrlParams();
  },
  methods: {
    parseUrlParams() {
      const urlParams = new URLSearchParams(window.location.search);
      const team1Param = urlParams.get('team1');
      const team2Param = urlParams.get('team2');
      this.team1 = this.parseTeamParam(team1Param);
      this.team2 = this.parseTeamParam(team2Param);
    },
    parseTeamParam(teamParam) {
      if (!teamParam) return [];
      teamParam = teamParam.toString().replace('[', '').replace(']', '');
      return teamParam.split(',').map(playerString => {
        const [id, name] = playerString.split('-');
        return {id: parseInt(id), name, kill: 0, assiste: 0, death: 0};
      });
    },
    calculateResults() {
      const team1Results = this.calculateTeamResults(this.team1);
      const team2Results = this.calculateTeamResults(this.team2);

      this.resultString = `${this.team1Score}-${this.team2Score};${team1Results};${team2Results}`;
    },
    calculateTeamResults(team) {
      return team.map(player => `${player.id}-${player.kill}-${player.assiste}-${player.death}`).join(';');
    },
    copyToClipboard() {
      const resultInput = this.$el.querySelector('input[readonly]');
      resultInput.select();
      document.execCommand('copy');
    }
  },
};
</script>

